/* =Contact
-------------------------------------------------------------- */

.contact-map {
  position: relative;
  height: 450px;
}

.inner-map {
  position: absolute;
  z-index: 1;
  padding: 25px;
  top: -410px;
  background: rgba(255, 255, 255, 0.8);
  display: table;
  height: 370px;
  width: calc(100% - 40px);
  margin-top: 20px;
  margin-left: 20px;
}

.inner-map .inner-map-content {
  display: table-cell;
  vertical-align: middle;
}

.inner-map .inner-map-content h1 {
  text-align: center;
  font-size: 60px;
  font-family: 'Roboto Slab', sans-serif;
}

.inner-map .inner-map-content hr {
  float: none;
}

.inner-map .inner-map-content p {
  text-align: center;
  font-size: 18px;
}

.contact-wrapper {
  clear: both;
  float: left;
  width: 100%;
}

.contact-info {
  font-size: 24px;
  clear: both;
  margin-bottom: 25px;
  float: left;
}

#map {
  width: 100%;
  height: 410px;
}

.icon-info {
  float: left;
  line-height: 45px;
}

.title-info {
  font-weight: 500;
}

.title-info,
.description-info {
  font-size: 14px;
  display: block;
  padding-left: 30px;
  letter-spacing: 1px;
}

.description-info {
  font-weight: 100;
}

.contact-form {}

.contact-form input[type="submit"] {
  width: 100%;
  height: 48px;
  outline: 0;
  border: 0;
  background-color: #7beec7;
  font-size: 18px;
  color: #fff;
  font-weight: 500;
  letter-spacing: 2pt;
}

.contact-form input,
.contact-form textarea {
  margin-bottom: 30px;
}

input {
  outline: 0;
}

textarea {
  outline: 0;
  width: 100%;
  height: 125px;
  padding-top: 7px;
  resize: none;
}

input[type="text"],
textarea {
  border: 1px solid #e6e6e6;
  padding-left: 10px;
}

input[type="text"]:focus,
textarea:focus {
  border: 1px solid #111;
}

input[type="text"] {
  height: 50px;
  width: 100%;
}